@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@400;700&display=swap);
.App {
	text-align: center;
	min-height: 60vh;
	display: flex;
	flex-direction: column;
	align-items: center;
}

#timer {
	font-family: "Dosis", "Roboto";
	color: #202020;
}

h1 {
	margin: 1rem;
	font-family: "Dosis", "Roboto";
	font-weight: bold;
	color: #383838;
}

#timer > p {
	margin: 1rem;
	padding: 0 1rem;
	font-size: large;
}

#timer > span {
	margin: 0rem;
	margin-top: 1rem;
	color: #707070;
}

#timer > img {
	margin: 4rem;
	height: 200px;
}

.form-page {
	display: flex;
	width: 100%;
	padding: 0 2rem;
}

@media screen and (max-width: 768px) {
 .form-page {
	 flex-direction: column;
 }
}

#form {
	padding: 1rem;
	width: 100%;
	font-family: "Dosis", "Roboto";
	color: #202020;
	display: flex;
	flex-direction: column;
	align-items: center;
}

#form > p {
	font-family: "Dosis", "Roboto";
	color: #4b4b4b90;
	font-size: 12px;
}

#form > * {
	margin: 1rem 0;
}

#form > .card {
	display: flex;
	flex-direction: column;
	border-radius: 0.5rem;
	padding: 1rem;
	width: 100%;
}

#form > .card > .divider {
	width: 100%;
	height: 2px;
	background-color: #bebebe90;
	margin: 1rem 0;
}

#form > .divider {
	width: 100%;
	margin: 0.5rem 0;
	border: 4px dashed #bebebe90;
}

#form > .card > input,
#form > .card > textarea,
#form > .card > button {
	font-family: "Dosis", "Roboto";
	color: #202020;
}

#form > .card > input,
#form > .card > textarea {
	border: 0;
	border-radius: 0.5rem;
	width: 100%;
}

#form > .card > input:focus,
#form > .card > textarea:focus {
	outline: none;
	box-shadow: none;
}

#form > .card > textarea {
	min-height: 20rem;
}

#form * button,
#form > label {
	text-decoration: none;
	text-transform: uppercase;
	font-family: "Dosis", "Roboto";
	font-weight: bold;
	font-size: 14px;
	border: 2px solid #707070;
	color: #707070;
	background-color: white;
	border-radius: 100px;
	outline: none;
	cursor: pointer;

	padding: 6px 24px;
	margin: 0 0.5rem;
	transition: box-shadow 0.3s, -webkit-transform 0.3s;
	transition: transform 0.3s, box-shadow 0.3s;
	transition: transform 0.3s, box-shadow 0.3s, -webkit-transform 0.3s;
}

#form > label {
	margin-top: 1rem;
}

@media (hover: hover) {
	#form * button:hover,
	#form > label:hover {
		box-shadow: 3px 3px 0px rgba(0, 0, 0, 0.26);
		-webkit-transform: translateY(-3px) translateX(-3px);
		        transform: translateY(-3px) translateX(-3px);
	}
}

#form > #error-para {
	color: red;
	font-weight: bold;
	margin: 0;
	font-size: 16px;
}

.gallery{
    /* -webkit-column-count: 3;
    -moz-column-count: 3; */
    -webkit-column-count: 3;
            column-count: 3;
    /* -webkit-column-width: 33%;
    -moz-column-width: 33%; */
    -webkit-column-width: 33%;
            column-width: 33%;
    padding: 5 12px;
    padding-left: 5 12px;
    padding-top: 5 12px;
    margin-top: 12px;
    margin-bottom: 12px;
    margin-right: 12px;
    margin-left: 12px;
}
.gallery .pics{
    transition: all 350ms ease;
    cursor: pointer;
    margin-bottom: 12px;
    margin-right: 12px;

}
.gallery .pics img:hover{
    opacity: 0.5;
    z-index: 2;
}
.gallery .pics img{
    /* object-fit: contain; */
    width: 90%;
    padding-right: -12px;
    padding-left: 20px;
    padding-top: 12px;
    padding-bottom: 12px;
    background-color: white;
    box-sizing: content-box;
    box-sizing: initial;
    

}
.pics .imgbox{
    border-width: 2px;
    border-color: black;
    border-style: solid;
}
@media (max-width:991px){
    .gallery{
        -webkit-column-count: 2;
        column-count: 2;
    }
}
@media (max-width:480px){
    .gallery{
        -webkit-column-count: 1;
        column-count: 1;
        -webkit-column-width: 100%;
        column-width: 100%;    
    }
}
.model{
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    visibility: hidden;
    transition: opacity .4s ease, visibility .4s ease, -webkit-transform .5s ease-in-out;
    transition: opacity .4s ease, visibility .4s ease, transform .5s ease-in-out;
    transition: opacity .4s ease, visibility .4s ease, transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
    overflow: hidden;
    z-index: 999;
    
}
.model.open{
    visibility: visible;
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);

}
.model img{
    width: auto;
    max-width: 100%;
    height: auto;
    max-height: 100%;
    display: block;
    line-height: 0;
    box-sizing: border-box;
    padding: 20px 0 20px;
    margin: 0 auto;
}
.model.open.caption{
    -webkit-text-decoration-color: aliceblue;
            text-decoration-color: aliceblue;
}
.model.open svg{
    position: fixed;
    top: 10px;
    right: 10px;
    width: 2rem;
    height: 2rem;
    padding: 5px;
    background-color: black;
    color: antiquewhite;
}

img {
    object-fit: cover;
}
